import { List as ImmutableList } from "immutable";
import { GridInfo, Hotel } from "./types";
import { NumberRange } from "@blueprintjs/core";

export interface State {
  grid?: GridInfo;
  hotels: ImmutableList<Hotel>;
  distanceRange: NumberRange;
  selectedSquare?: [string, string];
  hoveredSquare?: [string, string];
}

export interface LoadPricesAction {
  type: "LOAD_PRICES";
  grid: GridInfo;
}

export interface ToggleHotelSelection {
  type: "TOGGLE_HOTEL_SELECTION";
  hotel: Hotel;
}

export interface UpdateDistanceRangeAction {
  type: "UPDATE_DISTANCE_RANGE";
  range: NumberRange;
}

export interface SelectSquareAction {
  type: "SELECT_SQUARE";
  square: [string, string];
}

export interface HoverOverSquareAction {
  type: "HOVER_OVER_SQUARE";
  square: [string, string];
}

export interface UnhoverOverSquareAction {
  type: "UNHOVER_OVER_SQUARE";
}

export interface ResetSquareSelectionAction {
  type: "RESET_SQUARE_SELECTION";
}

export type Action =
  | LoadPricesAction
  | UpdateDistanceRangeAction
  | SelectSquareAction
  | HoverOverSquareAction
  | UnhoverOverSquareAction
  | ToggleHotelSelection
  | ResetSquareSelectionAction;

export function matrixReducer(state: State, action: Action) {
  switch (action.type) {
    case "LOAD_PRICES":
      return {
        ...state,
        grid: action.grid
      };
    case "UPDATE_DISTANCE_RANGE":
      return {
        ...state,
        distanceRange: action.range
      };
    case "SELECT_SQUARE":
      return {
        ...state,
        selectedSquare: action.square
      };
    case "RESET_SQUARE_SELECTION":
      return {
        ...state,
        selectedSquare: undefined
      };
    case "HOVER_OVER_SQUARE":
      return {
        ...state,
        hoveredSquare: action.square
      };
    case "UNHOVER_OVER_SQUARE":
      return {
        ...state,
        hoveredSquare: undefined
      };
    case "TOGGLE_HOTEL_SELECTION":
      return {
        ...state,
        hotels: state.hotels.map(hotel => ({
          ...hotel,
          selected:
            hotel.name === action.hotel.name ? !hotel.selected : hotel.selected
        }))
      };
    default:
      throw new Error("Unknown action dispatched");
  }
}
