import React, { useEffect, useReducer, useMemo, Fragment } from "react";
import { HTMLTable, Divider, Checkbox } from "@blueprintjs/core";
import { CheckOutRow } from "./CheckOutRow";
import "./MarriottMatrix.scss";
import {
  PricePoint,
  PriceGrid,
  Bucket,
  Hotel,
  PricePointWithPrice
} from "./types";
import { List as ImmutableList } from "immutable";
import { matrixReducer, State } from "./matrixReducer";
import { AvailableRooms } from "./AvailableRooms";
import { DistanceSlider } from "./DistanceSlider";
import { DEFAULT_HOTELS } from "./utils";

export function datesToKey(
  dates: Pick<PricePoint, "checkIn" | "checkOut">
): string {
  return `${dates.checkIn}-${dates.checkOut}`;
}

function sortDates(dates: Set<string>): string[] {
  return Array.from(dates).sort();
}

interface Props {
  pricePoints: PricePoint[];
}

function createInitialState(props: Props): State {
  const selectedHotelNames = new Set<string>();
  const hotels: Hotel[] = [];
  props.pricePoints.forEach(pricePoint => {
    const name = pricePoint.hotelName;
    if (!selectedHotelNames.has(name)) {
      selectedHotelNames.add(name);
      hotels.push({
        name: name,
        distance: pricePoint.distance,
        selected: DEFAULT_HOTELS.includes(name)
      });
    }
  });

  hotels.sort((a, b) => a.distance - b.distance);

  return {
    hotels: ImmutableList(hotels),
    distanceRange: [0, 1.4]
  };
}

export const MarriottMatrix: React.FC<Props> = (props: Props) => {
  const [state, dispatch] = useReducer(
    matrixReducer,
    createInitialState(props)
  );

  const bucketedHotels = useMemo(() => {
    const hotels = state.hotels;
    if (hotels.size === 0) {
      return [];
    }
    const buckets: Bucket<Hotel>[] = [];
    hotels.forEach(hotel => {
      if (!hotelIsWithinSelectedDistance(hotel)) {
        // return;
      }
      if (
        buckets.length === 0 ||
        hotel.distance > buckets[buckets.length - 1].ceiling
      ) {
        buckets.push({
          ceiling: Math.ceil(hotel.distance),
          items: [hotel]
        });
      } else {
        buckets[buckets.length - 1].items.push(hotel);
      }
    });
    return buckets;
  }, [state.hotels, state.distanceRange]);

  const nameToHotel = useMemo(() => {
    const map = new Map<string, Hotel>();
    state.hotels.forEach(hotel => {
      map.set(hotel.name, hotel);
    });
    return map;
  }, [state.hotels]);

  const anyHotelsSelected = useMemo(() => {
    return state.hotels.some(hotel => hotel.selected);
  }, [state.hotels]);

  useEffect(() => {
    const priceGrid: PriceGrid = new Map();
    const checkInDates = new Set<string>();
    const checkOutDates = new Set<string>();

    props.pricePoints.forEach(pricePoint => {
      const {
        hotelName,
        pricePerNight,
        soldOut,
        corporateRate,
        distance,
        ...dates
      } = pricePoint;

      if (pricePerNight === null || pricePerNight === undefined || soldOut) {
        return;
      }

      const hotelForPricePoint = nameToHotel.get(hotelName);
      if (
        !hotelForPricePoint ||
        !hotelIsFilteredTo(hotelForPricePoint) ||
        !hotelIsWithinSelectedDistance(hotelForPricePoint)
      ) {
        return;
      }

      const dateString = datesToKey(dates);
      const bestPriceForStay = priceGrid.get(dateString);
      if (!bestPriceForStay || pricePerNight < bestPriceForStay.pricePerNight) {
        checkInDates.add(dates.checkIn);
        checkOutDates.add(dates.checkOut);
        priceGrid.set(dateString, {
          ...pricePoint,
          pricePerNight: pricePerNight
        });
      }
    });

    dispatch({
      type: "LOAD_PRICES",
      grid: {
        grid: priceGrid,
        checkInDates: sortDates(checkInDates),
        checkOutDates: sortDates(checkOutDates)
      }
    });
  }, [props.pricePoints, state.hotels, state.distanceRange]);

  return (
    <div className="container">
      <div className="sidebar">{renderFilters()}</div>
      <div className="content">
        {renderTable()}
        <Divider />
        {state.selectedSquare && (
          <AvailableRooms
            dates={state.selectedSquare}
            pricePoints={getPricePointsForStay(state.selectedSquare)}
          />
        )}
      </div>
    </div>
  );

  function renderFilters() {
    const closestHotel = state.hotels.get(0);
    const farthestHotel = state.hotels.get(state.hotels.size - 1);
    const min = closestHotel ? closestHotel.distance : 0;
    const max = farthestHotel ? farthestHotel.distance : 50;
    return (
      <div className="filters">
        <DistanceSlider
          min={min}
          max={max}
          value={[
            Math.max(min, state.distanceRange[0]),
            Math.min(max, state.distanceRange[1])
          ]}
          onChange={range =>
            dispatch({
              type: "UPDATE_DISTANCE_RANGE",
              range
            })
          }
        />
        <div className="hotels">
          {bucketedHotels.map(bucket => (
            <Fragment key={`${bucket.ceiling}`}>
              <h3>{`Under ${bucket.ceiling} ${
                bucket.ceiling === 1 ? "mile" : "miles"
              } away`}</h3>
              {bucket.items.map(hotel => (
                <Checkbox
                  checked={
                    hotel.selected && hotelIsWithinSelectedDistance(hotel)
                  }
                  disabled={!hotelIsWithinSelectedDistance(hotel)}
                  onChange={() =>
                    dispatch({
                      type: "TOGGLE_HOTEL_SELECTION",
                      hotel: hotel
                    })
                  }
                  key={hotel.name}
                  label={hotel.name}
                />
              ))}
            </Fragment>
          ))}
        </div>
      </div>
    );
  }

  function renderTable() {
    if (!state.grid) {
      return null;
    }
    return (
      <div className="table-container">
        <div>
          <p>
            <i>Check in:</i>
          </p>
          <HTMLTable bordered condensed className="html-table">
            <thead>
              <CheckOutRow
                checkInDates={state.grid.checkInDates}
                shouldTint={shouldTintSquare()}
              />
            </thead>
            <tbody>
              {state.grid.checkOutDates.map(checkOut =>
                state.grid ? (
                  <CheckOutRow
                    key={checkOut}
                    checkOut={checkOut}
                    checkInDates={state.grid.checkInDates}
                    grid={state.grid.grid}
                    onClick={onSelectSquare(checkOut)}
                    activeCheckIn={getActiveCheckIn(checkOut)}
                    getPricePercentile={getPricePercentile(checkOut)}
                    shouldTint={shouldTintSquare(checkOut)}
                    onHover={onHoverSquare(checkOut)}
                    offHover={onUnhoverSquare(checkOut)}
                  />
                ) : null
              )}
            </tbody>
          </HTMLTable>
        </div>
        <i className="checkout-text">Check out:</i>
      </div>
    );
  }

  function hotelIsFilteredTo(hotel: Hotel): boolean {
    return !anyHotelsSelected || hotel.selected;
  }

  function onSelectSquare(checkOut: string) {
    return (checkIn: string) => {
      if (
        state.selectedSquare &&
        checkIn === state.selectedSquare[0] &&
        checkOut === state.selectedSquare[1]
      ) {
        dispatch({
          type: "RESET_SQUARE_SELECTION"
        });
      } else {
        dispatch({
          type: "SELECT_SQUARE",
          square: [checkIn, checkOut]
        });
      }
    };
  }

  function onHoverSquare(checkOut: string) {
    return (checkIn: string) => {
      dispatch({
        type: "HOVER_OVER_SQUARE",
        square: [checkIn, checkOut]
      });
    };
  }

  function onUnhoverSquare(checkOut: string) {
    return (checkIn: string) => {
      dispatch({
        type: "UNHOVER_OVER_SQUARE"
      });
    };
  }

  function shouldTintSquare(checkOut?: string) {
    return (checkIn: string) => {
      if (!state.hoveredSquare) {
        return false;
      }
      if (state.hoveredSquare[0] === checkIn) {
        return checkOut === undefined || state.hoveredSquare[1] >= checkOut;
      }
      if (state.hoveredSquare[1] === checkOut) {
        return state.hoveredSquare[0] <= checkIn;
      }
      return false;
    };
  }

  function getActiveCheckIn(checkOut: string) {
    if (!state.selectedSquare) {
      return;
    }
    if (checkOut === state.selectedSquare[1]) {
      return state.selectedSquare[0];
    }
  }

  function getPricePercentile(checkOut: string) {
    return (checkIn: string) => {
      if (!state.grid) {
        return -1;
      }
      const thisSquare = state.grid.grid.get(datesToKey({ checkIn, checkOut }));
      if (thisSquare === undefined) {
        return 0.5;
      }

      let numMoreExpensive = 0;
      for (const otherValue of state.grid.grid.values()) {
        if (otherValue.pricePerNight > thisSquare.pricePerNight) {
          numMoreExpensive += 1;
        }
      }

      return 1 - numMoreExpensive / state.grid.grid.size;
    };
  }

  function hotelIsWithinSelectedDistance(hotel: Hotel) {
    return (
      hotel.distance >= state.distanceRange[0] &&
      hotel.distance <= state.distanceRange[1]
    );
  }

  function getPricePointsForStay([checkIn, checkOut]: [
    string,
    string
  ]): PricePointWithPrice[] {
    if (!state.grid) {
      return [];
    }
    return props.pricePoints.reduce<PricePointWithPrice[]>(
      (all, pricePoint) => {
        const hotel = nameToHotel.get(pricePoint.hotelName);
        if (
          hotel &&
          hotelIsFilteredTo(hotel) &&
          pricePoint.checkIn === checkIn &&
          pricePoint.checkOut === checkOut &&
          pricePoint.pricePerNight !== undefined &&
          pricePoint.pricePerNight !== null
        ) {
          all.push({
            ...pricePoint,
            pricePerNight: pricePoint.pricePerNight
          });
        }
        return all;
      },
      []
    );
  }
};
