import React from "react";
import classNames from "classnames";
import { datesToKey } from "./MarriottMatrix";
import "./CheckOutRow.scss";
import { PriceGrid } from "./types";
import { formatDate } from "./utils";

interface Props {
  checkInDates: string[];
  checkOut?: string;
  grid?: PriceGrid;
  activeCheckIn?: string;
  shouldTint: (checkIn: string) => boolean;
  onClick?: (checkIn: string) => void;
  getPricePercentile?: (checkIn: string) => number;
  onHover?: (checkIn: string) => void;
  offHover?: (checkIn: string) => void;
}

export const CheckOutRow: React.FC<Props> = (props: Props) => {
  return (
    <tr>
      {props.checkInDates.map(checkIn => {
        if (props.grid && props.checkOut) {
          const pricePoint = props.grid.get(
            datesToKey({ checkOut: props.checkOut, checkIn })
          );
          return (
            <td
              key={checkIn}
              onClick={
                pricePoint && (() => props.onClick && props.onClick(checkIn))
              }
              onMouseEnter={() => props.onHover && props.onHover(checkIn)}
              onMouseLeave={() => props.offHover && props.offHover(checkIn)}
              className={classNames(
                pricePoint && "clickable",
                props.activeCheckIn === checkIn && "active",
                getColorClass(checkIn),
                props.shouldTint(checkIn) && "tint"
              )}
            >
              <p>{pricePoint && `$${pricePoint.pricePerNight}`}</p>
            </td>
          );
        } else {
          return (
            <th
              key={checkIn}
              className={classNames(
                "checkin-date",
                props.shouldTint(checkIn) && "tint"
              )}
            >
              <p>{formatDate(checkIn)}</p>
            </th>
          );
        }
      })}
      <th>
        <p>{formatDate(props.checkOut)}</p>
      </th>
    </tr>
  );

  function getColorClass(checkOut: string) {
    if (!props.getPricePercentile) {
      return;
    }
    const percentile = props.getPricePercentile(checkOut);
    if (percentile >= 0.9) {
      return "cost-1";
    } else if (percentile > 0.7) {
      return "cost-2";
    } else if (percentile > 0.3) {
      return "cost-3";
    } else if (percentile > 0.1) {
      return "cost-4";
    } else {
      return "cost-5";
    }
  }
};
