import React from "react";
import { NumberRange, RangeSlider } from "@blueprintjs/core";
import "./DistanceSlider.scss";

interface Props {
  value: NumberRange;
  onChange: (range: NumberRange) => void;
  min: number;
  max: number;
}

export const DistanceSlider: React.FC<Props> = (props: Props) => {
  return (
    <div className="distance-slider-container">
      <RangeSlider
        className="distance-slider"
        min={props.min}
        max={props.max}
        labelRenderer={label => (
          <span className="no-wrap">{label.toLocaleString() + " miles"}</span>
        )}
        labelStepSize={5}
        stepSize={0.1}
        {...props}
      />
    </div>
  );
};
